<template lang="pug">
  .main_wrapper
    .header
      img.logo(src="static/logo.svg")
      h1 Oniqum
    Modal(v-if="h === '#1' || h === '#2'")
    ModalGoods(v-if="h === '#2'")
    ModalStatus(v-if="h === '#3'")
    .scroll
      .left_panel
        v-calendar(:attributes='attrs')
        .createButton Создать новый статус заказа

      List(v-for="i in a")






</template>

<script>
import _ from 'lodash';
import Vue from 'vue';
import VCalendar from 'v-calendar';
import { mapGetters } from 'vuex';
import List from '@/components/List';
import Modal from '@/components/Modal';
import ModalGoods from '@/components/ModalGoods';
import ModalStatus from '@/components/ModalStatus';

import 'v-calendar/lib/v-calendar.min.css';

// import moment from 'moment';
// import i18n from '@/lib/i18n';
// import axios from 'axios';

Vue.use(VCalendar, { locale: 'ru' });

export default {
  components: {
    List,
    Modal,
    ModalGoods,
    ModalStatus,
  },
  mounted() {
    console.log(_, mapGetters, Vue);
    console.log(VCalendar);
  },
  computed: {
    a() {
      return ['new', 'finished', 'proc', 'unproc'];
    },
    h() {
      return this.$route.hash;
    },
  },
  methods: {
  },
  data() {
    return {
      attrs: [
        {
          key: 'today',
          dates: new Date(),
          highlight: {
            backgroundColor: '#00ABF7',
          },
        },
        {
          key: 'today',
          dates: new Date(2019, 3, 20),
          dot: {
            backgroundColor: '#00ABF7',
          },
        },
      ],
    };
  },
};

</script>
<style lang="scss" scoped>
  body {
    font-family: 'Roboto';
  }
  .main_wrapper {
    padding-top: 80px;

    .header {
      height: 60px;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 10;
      background: white;
      text-align: center;
      padding: 5px;
      .logo {
        display: inline-block;
        position: relative;
        top: -3px;
      }
      h1 {
        font-weight: bold;
        display: inline-block;
        font-size: 22px;
        margin-top: 10px;
        margin-left: 15px;
      }
    }
    .scroll {
      display: flex;
      z-index: 5;
      padding: 20px 0;
      padding-right: 2000px;
    }
    .left_panel {
      display: flex;
      flex-flow: column nowrap;
      min-width: 400px;
      max-width: 400px;
      background: white;
      padding: 0 20px;
      .createButton {
        background: #338BC9;
        border-radius: 5px;
        padding: 10px 15px;
        margin: 30px auto;
        font-weight: bold;
        font-size: 20px;
        line-height: normal;
        cursor: pointer;
        color: #FFFFFF;

      }
    }
  }
</style>



