<template lang="pug">
  div#app
    vue-progress-bar
    router-view
</template>

<script>

import { mapState } from 'vuex';

export default {

  computed: {
    ...mapState({
      loading: state => state.loading,
    }),
  },
  watch: {
    loading(val) {
      if (val === true) {
        this.$Progress.start();
      } else if (val === false) {
        this.$Progress.finish();
      } else {
        this.$Progress.fail();
      }
    },
  },
};
</script>

<style lang="scss">
  @import '~@/assets/styles/index.scss';
</style>



