<template lang="pug">
  .list(:style="style")
    .header В работе
    .pseudo Новый заказ
    Order(v-for="i in b")
 </template>

<script>

// import { mapMutations } from 'vuex';
// import _ from 'lodash';
import Order from '@/components/Order';

export default {
  name: 'List',
  components: {
    Order,
  },
  methods: {

  },
  computed: {
    b() {
      return Math.random() > 0.5 ? [1, 2, 3] : [1, 3, 2, 4];
    },
    style() {
      return '';
    },
  },
};
</script>

<style scoped lang="scss">
  .list {
    .header {
      width: 300px;
      text-align: center;
      border: 1px solid #FFB500;
      box-sizing: border-box;
      border-radius: 5px;
      font-weight: bold;
      font-size: 16px;
      padding: 10px 5px;
    }
    .pseudo {
      margin: 20px;
      font-weight: bold;
      font-size: 20px;
      color: #757575;
      text-align: center;
      cursor: pointer;
    }
    display: flex;
    flex-flow: column nowrap;
    min-width: 330px;
    max-width: 330px;
    margin-right: 25px;
    padding: 15px;
    box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.5);
    &:last-child {
      margin-right: 100px;
    }
  }
</style>



