/* eslint-disable */

import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state: {
    loading: false,
    currentCat: false,
  },
  actions: {

  },
  getters: {
    getCat(state) {
      return state.currentCat;
    },
  },
  mutations: {
    setLoading(state, val) {
      state.loading = val;
    },
    setCat(state, val) {
      state.currentCat = val;
    },
  },
});



