import Vue from 'vue';
import Router from 'vue-router';
import MainPage from '@/containers/MainPage';
import Login from '@/containers/Login';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: MainPage,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
  ],
});



