          b 12:33
        .money
          img.i(src="static/idollar.svg")
          b 999 000 Руб.
    .descr Если вашему гостю необходимо парковочное место
    .params
      .param
        img.i(src="static/igeo.svg")
        span Ленина 1б, кв. 1
      .param
        img.i(src="static/ibdollar.svg")
        span Ленина 1б, кв. 1
      .param
        img.i(src="static/ibmoney.svg")
        span Ленина 1б, кв. 1
      .param
        img.i(src="static/ibdollar.svg")
        span Ленина 1б, кв. 1
    .chat_button
        img.i(src="static/ichat.svg")
        span Перейти в чат


 </template>

<script>

// import { mapMutations } from 'vuex';
// import _ from 'lodash';
// import Order from '@/components/Order';

export default {
  name: 'List',
  components: {
    // Order,
  },
  methods: {

  },
  computed: {
    b() {
      return [1, 2, 3];
    },
    style() {
      return '';
    },
    name() {
      return (Math.random() > 0.5) ? 'Константин Перекатипольсковатый' : 'Анна';
    },
  },
};
</script>

<style scoped lang="scss">
  .order {
    border: 1px solid #28C96D;
    box-sizing: border-box;
    border-radius: 5px;
    width: 300px;
    margin-bottom: 15px;
    .header {
      background: #F7F9FA;
      border-radius: 5px;
      display: block;
      position: relative;
      padding-bottom: 15px;

      .user {
        .ava {
          width: 36px;
          height: 36px;
          border-radius: 18px;
          margin: 12px 17px;
          display: inline-block;
          vertical-align: top;
        }
        .name {
          display: inline-block;
          width: 100px;
          font-weight: bold;
          font-size: 17px;
          line-height: 1.1;
          margin-top: 12px;
        }
      }
      .bars {
        .time {
          height: 34px;
          border-radius: 17px;
          display: inline-block;
          background: #FFFFFF;
          box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
          margin-left: 17px;
          box-sizing: border-box;
          padding: 3px 10px;
          font-size: 15px;
          color: #E05A5D;
          b {
            vertical-align: text-top;
            padding-left: 10px;
          }
        }
        .money {
          height: 34px;
          border-radius: 17px;
          display: inline-block;
          float: right;
          background: #FFFFFF;
          box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
          margin-right: 17px;
          box-sizing: border-box;
          padding: 3px 10px;
          font-size: 15px;
          color: black;
          b {
            vertical-align: middle;
            padding-left: 10px;
          }
        }
      }
    }
    .descr {
      font-weight: 500;
      font-size: 16px;
      padding: 10px 17px;
      color: #222222;
    }
    .params {
      .param {
        display: block;
        width: 100%;
        margin-bottom: 10px;
      }
      .i {
        margin: 0 17px;
      }
      span {
        font-weight: 500;
        font-size: 14px;
      }
    }
    .chat_button {
      font-weight: bold;
      font-size: 16px;
      border-top: 0.25px solid #B8B8B8;
      height: 45px;
      width: 100%;
      text-align: center;
      padding-top: 10px;
      color: #535F6E;
      .i {
        margin-right: 10px;
      }
    }
  }
</style>



